<script>
import eTree from '../../../components/eTree.vue'

export default {
  name: 'phoneConfig',
  components: {
    eTree
  },
  data() {
    return {
      title: '',
      dialogFormVisible: false,
      formLabelWidth: '120px',
      showBar: false,
      form: {
        name: '',
        SIP: '',
        type: '',
        stats: ''
      },
      rechargeInfo: {
        value: null
      },
      addForm: {
        name: '', //
        type: '', // 
        area: '', //
        SIP: '', //sip
        password: '', // 
        balance: '', //
        overdraft: '', //
        // stats: '', //
        recordingStatus: '', //
        extranetCall: '', //
        billingModel: '', //
        combo: '', //
        mandatoryShow: '',
        mandatoryShowNum: '',
        enforceNumber: '', //
        coordinateNumber: '', //
        remark: '' //
      },
      list: [],
      type: [
        {label: '', value: ''},
        {label: '', value: ''},
        {label: '4G', value: '4G'},
        {label: '5G', value: '5G'},
        {label: 'APP', value: 'APP'},
        {label: 'APP', value: 'APP'}
      ],
      billingModel: [
        {label: '1 + 1', value: '1 + 1'},
        {label: '6 + 6', value: '6 + 6'},
        {label: '12 + 12', value: '12 + 12'},
        {label: '60 + 60', value: '60 + 60'}
      ],
      relayType: [
        {label: '', value: ''},
        {label: '', value: ''},
      ],
      combo: [
        {label: '', value: ''},
        {label: '', value: ''},
        {label: '', value: ''}
      ],
      rules: {
        name: [
          {required: true, message: '', trigger: 'blur'},
          {max: 100, message: '100', trigger: 'blur'}
        ],
        type: [{required: true, message: '', trigger: 'blur'}],
        area: [{required: true, message: '', trigger: 'blur'}],
        SIP: [{required: true, message: 'SIP', trigger: 'blur'}],
        password: [{required: true, message: '', trigger: 'blur'}],
        mandatoryShowNum: [{required: false, message: '', trigger: 'blur'}],
        billingModel: [{required: false, message: '', trigger: 'blur'}],
        combo: [{required: false, message: '', trigger: 'blur'}],
        extranetCall: [{required: false, message: '', trigger: 'blur'}],
        recordingStatus: [{required: false, message: '', trigger: 'blur'}],
        overdraft: [{required: false, message: '', trigger: 'blur'}],
        balance: [{required: false, message: '', trigger: 'blur'}],
        mandatoryShow: [{required: false, message: '', trigger: 'blur'}],
        enforceNumber: [{required: false, message: '', trigger: 'blur'}],
        remark: [{required: false, message: '', trigger: 'blur'}]
      }
    }
  },
  methods: {
    submitForm(addForm) {

      this.$refs['addForm'].validate((valid) => {
        if (valid) {
          this.$message({
            message: '',
            type: 'success'
          })
          this.list.push(this.addForm)
          window.localStorage.setItem('phone', JSON.stringify(this.list))
          this.dialogFormVisible = false
        } else {
          this.$message.error(' ')
          return false
        }
      })
    },
    addForms(row, title) {
      this.dialogFormVisible = true
      this.title = title
      if (title === '') {
        this.addForm = row
      } else {
        this.resetForm('addForm')
        console.log('aaa')
      }
    },
    tableRowClassName({row, rowIndex}) {
      row.index = rowIndex
    },
    removeIt(row) {
      this.list.map((item, i) => {
        if (row === item) {
          this.list.splice(i, 1)
        }
      })
    },
    resetForm() {
      this.$refs['addForm'].resetFields()
    },
    recharge(row) {
      this.showBar = true
      this.rechargeInfo = row
    },
    submit(rechargeInfo) {
      rechargeInfo.value !== null
          ? (this.list[rechargeInfo.index].balance =
              parseInt(rechargeInfo.balance) + parseInt(rechargeInfo.value))
          : ''
      this.$message({
        message: '',
        type: 'success'
      })
      this.showBar = false
    }
  },

  created() {
    this.list = JSON.parse(window.localStorage.getItem('phone')) || []
  }
}
</script>

