        diaplanName: '', //
        diaplanPrefix: '',  //
      },
      trunkGroup: [],
      programStatus: [
        {label: '', value: ''},
        {label: '', value: ''}
      ],
      rules: {
        diaplanGatewayGroup: [{required: true, message: ' ', trigger: 'blur'}],
        diaplanName: [{required: true, message: ' ', trigger: 'blur'}],
        diaplanPrefix: [{required: true, message: ' ', trigger: 'blur'}]
      }
    }
  },
  methods: {
    getDaiPlan(form) {
      diaPlanList(form).then(res => {
        console.log(res)
        this.list = res.data.data.records
      }).catch(e => {
        console.log(e)
      })
    },

    //
    onSubmit() {
    },
    //
    clearSubmit() {
    },
    showAddForm(row, title) {
      this.dialogFormVisible = true
      this.title = title
      if (title === '') {
        this.addForm = row
      } else if (title === '') {
        this.addForm = row
        this.isReadOnly = true
      } else {
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          /**
           * 
           */
          addDiaPlanList(this.addForm).then(res => {
            console.log(res)
          }).catch(e => {
            console.log(e)
          })
          this.$message({
            message: '',
            type: 'success'
          })
        } else {
          this.$message({
            message: ' ',
            type: 'error'
          })
          return false
        }
      })
    }
  },
  created() {
    this.getDaiPlan(this.form)
  },
  watch: {
    dialogFormVisible(val) {
      if (val === true) {
        getGwgroup(this.form)
        {
          getGwgroup(this.form).then(res => {
            this.trunkGroup = res.data.data.records
          }).catch(e => {
            this.$message.error(e)
          })
        }
      }
    }
  }
}
</script>

